'use strict';

(function (factory) {

	"use strict";

	// simply the dependanices required for this module pass in either required module names or the raw objects if amd not found

	if (typeof define === 'function' && define['amd']) {
		define(['knockout'], factory);
	}
})(function (ko) {

	"use strict";

	ko.bindingHandlers.tabs = {
		init: function init(root) {

			utils.forEach(root.querySelectorAll('.tab-header'), function (i, element) {

				element.addEventListener('click', function () {

					utils.removeClass(root.querySelectorAll('.tab-selected'), 'tab-selected');
					utils.addClass(this, 'tab-selected');
					utils.addClass(root.querySelectorAll(this.getAttribute('data-tabs-target')), 'tab-selected');
				});
			});
		}
	};

	var utils = {
		addClass: function addClass(items, className) {

			items = items.length > 0 ? items : [items];

			utils.forEach(items, function (i, el) {

				if (el.classList) {
					el.classList.add(className);
				} else {
					el.className += ' ' + className;
				}
			});
		},
		removeClass: function removeClass(items, className) {

			items = items.length > 0 ? items : [items];

			utils.forEach(items, function (i, el) {

				if (el.classList) {
					el.classList.remove(className);
				} else {
					el.className = el.className.replace(new RegExp('(^|\\b)' + className.split(' ').join('|') + '(\\b|$)', 'gi'), ' ');
				}
			});
		},
		forEach: function forEach(array, callback, scope) {

			for (var i = 0; i < array.length; i++) {
				callback.call(scope, i, array[i]);
			}
		}
	};
});